@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
html {
  font-family: "Poppins", sans-serif;
}

p {
  margin-bottom: 0;
}

em {
  font-size: 12px;
  color: red;
}

.App {
  text-align: center;
}

.main {
  margin: 0 auto;
  width: 75vw;
}

.todo-card{display:flex;flex-direction:column;border:1px solid;border-radius:10px;background-color:#cddcdc;padding:20px}.todo-card__status--done{color:green}.todo-card__status--undone{color:coral}


.main__container{margin:0 auto;width:75vw;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:10px;border-radius:10px}

.main__form{border-radius:10px;margin:10px 0}.main__form__fields{margin:10px;padding:5px;width:250px}.main__form__errors-wrapper{height:20px}.main__form-button{margin:10px;width:100px;height:30px;border-radius:5px;outline:none;cursor:pointer}

